.formDiv {
    
    padding: 100px;
    background-color: #f5ee21;
    z-index: 2;
    /* border-bottom-style : solid;
    border-width: 3px;
    border-bottom-color:black; */
    margin: auto;
}

.mainDiv {
    background-color:  #4299b6;
    z-index: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    height: 100vh;
}

.signInHeading {
    font-family: 'Kaushan Script', cursive;  
    padding-bottom: 20px;
    color: #4299b6;
}

::-webkit-input-placeholder {
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    text-align: center;
}

:-ms-input-placeholder {
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    text-align: center;
}

::-ms-input-placeholder {
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    text-align: center;
}

::placeholder {
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    text-align: center;
}

.formDiv button {
    padding: 10px 95px;
    margin: 30px;
    border-radius: 5px;
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    color: white;
    border-color: #4299b6;
    background-color: #4299b6
}



